header {
  margin-left: 5rem;
  margin-top: 80px;
}

header h1 {
  text-transform: uppercase;
  font-size: 3rem;
  margin: 3rem 0 0;
}

@media only screen and (max-width: 767px) {
  header {
    width: 70%;
    padding-right: 10%;
    margin-top: 50px;
    margin-bottom: 80px;
  }
}

header p {
  margin: 0;
}

section {
  margin: 100px 0 50px 50px;
  width: 90%;
  -webkit-columns: 2;
          columns: 2;
  -webkit-column-gap: 5%;
          column-gap: 5%;
}

@media only screen and (max-width: 767px) {
  section {
    width: 100%;
    margin: 0;
    -webkit-columns: 1;
            columns: 1;
  }
}



