.image-wrapper {
  display: inline-block;
  height: 0;
  position: relative;
  width: 100%;
  margin: 80px 0;
}

@media only screen and (max-width: 992px) {
  .image-wrapper {
    width: 100%;
  }
}

@media only screen and (max-width: 767px) {
  .image-wrapper {
    margin: 40px 0;
  }
}

.image-wrapper img {
  position: absolute;
  width: 100%;
}

.screen {
  width: 100%;
  height: 0;
  position: absolute;
  bottom: 0;
  z-index: 9999;
  background-color: #000;
  opacity: 1 !important;
}

.down + .screen.screen {
  -webkit-animation-name: reveal;
          animation-name: reveal;
  -webkit-animation-duration: 3s;
          animation-duration: 3s;
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes reveal {
  100% {
    padding-bottom: 0;
  }
}

@keyframes reveal {
  100% {
    padding-bottom: 0;
  }
}

.ready {
  opacity: 0;
}



